<template>

  <div>

        <h3>Ke sputn testu mus splnit minimln 40 cvien s celkovou spnost 85%</h3>
        <h3>Splnil si: {{user.total_count_of_excercies}} cvien</h3>
        <h3>Tvoje spnost je: {{Math.floor(user.success_rate)}}%</h3>

        <section v-if="main_control()===true">
          <h2>Tvoje podmnky k absolvovn testu jsou splnn</h2>
          <button @click="to_test">Pejt k testu</button>
        </section>
      </div>


</template>

<script>



export default {
  name: "controlComponent",

  data(){
    return{
      user: JSON.parse(localStorage.user)
    }
  },

  methods:{

    main_control(){
      // true X false

      return (this.user.total_count_of_excercies >= 40) && (this.user.success_rate >= 85);
    },

    to_test(){
      console.log(this.main_control());
      this.$router.push('./finalExam');
    }

  }



}
</script>

